@font-face {font-family: "DS-Digital";
	src: url(/rect-snek/static/media/DS-Digital.37c8019a.eot);
  src:
    url(/rect-snek/static/media/DS-Digital-iefix.37c8019a.eot) format("embedded-opentype"),
    url(/rect-snek/static/media/DS-Digital.ce409a4d.woff2) format("woff2"),
		url(/rect-snek/static/media/DS-Digital.223f2246.ttf) format("truetype"),
		url(/rect-snek/static/media/DS-Digital.cad634a7.svg) format("svg");
}
body {
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  background-color: #D9DBC5;
}
.root {
  width: 100%;
  height: 100%;
  margin:0;
  line-height: 0;
  padding-top: 1.25vh;
}
.snek {
  position: relative;
  margin: 0 auto;
  width: 42vh;
  height: 85vh;
}
.button {
  background: linear-gradient(to bottom right,#ffe033 0%,#ffd700 100%);
  cursor:pointer;
  box-shadow:
    0 -.5vh 0 0 #e6c300,
    0 -.5vh 0 .25vh #b8baab,
    inset -.25vh -.25vh 3px 0 #e6c300,
    inset .25vh .25vh .5vh 0 #fff033;
}
.button:active, .button_pressed {
  -webkit-transform: translateY(-.5vh);
          transform: translateY(-.5vh);
  box-shadow:
    0 0 0 .25vh #b8baab,
    0 .5vh .5vh #f4f7ee,
    inset 0 0 .25vh #b39800,
    inset 0 .25vh .25vh #ccad00;
}
.display {
  width: 100%;
  height: 55vh;
}
.display__edging {
  width: 35vh;
  margin: 0 auto;
  padding: 0 1vh 1vh;
  border: .5vh solid #911;
}
.display__legend {
margin: 0 auto;
height: 3vh;
width: 18vh;
background-size: 100% 100%;
background-image: url(/rect-snek/static/media/legend.9a918d49.svg);
border-left: 1vh solid #D9DBC5;
border-right: 1vh solid #D9DBC5;
}
.display__borders {
  margin: 0 auto;
  width: 33vh;
  height: 43.5vh;
  background-color: #909D83;
  border-width: 2.5vh;
  border-radius: .5vh;
  border-style: solid;
  border-top-color: #d6d8c3;
  border-left-color: #bdbfae;
  border-bottom-color: #caccb7;
  border-right-color: #e3e5ce;
}

.display__glass {
  width: 100%;
  height: 100%;
  border-radius: .5vh;
  box-shadow:inset .25vh .25vh 1vh 0 #313729, 1vh 0 1vh 0 #f8f9e3, -1vh 0 1vh 0 #97998c;
}
.display__frame {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  float: left;
  width: 20vh;
  height: 40vh;
  margin: 4% 0 4% 3%;
  padding: .5vh;
  outline: .5vh solid #313729;
}
.display__pixel {
  width: 2vh;
  height: 2vh;
  fill: #7e916c;
}
.display__side {
  position: relative;
  float: right;
  width: 11vh;
  color: #313729;
}
.display__score {
  text-align: center;
  font-family: arial;
  font-style: italic;
  font-size: 1.25vh;
  line-height: 2vh;
  margin: 1vh auto;
}
.display__score-value {
  margin: 0;
  font-style: normal;
  font-family: "DS-Digital";
  font-size: 2.5vh;
  line-height: 2.5vh;
}
.display__hi-score {
  text-align: center;
  font-family: arial;
  font-style: italic;
  font-size: 1.25vh;
  line-height: 2vh;
  margin-bottom: 1vh;
}
.display__hi-score-value {
  margin: 0;
  font-style: normal;
  font-family: "DS-Digital";
  font-size: 2.5vh;
  line-height: 2.5vh;
}
.display__level {
  text-align: center;
  font-family: arial;
  font-style: italic;
  font-size: 1.25vh;
  line-height: 2vh;
  margin-top: 2vh;
  margin-bottom: 1.25vh;
}
.display__level-value {
  margin: 0;
  font-style: normal;
  line-height: 5vh;
  font-family: "DS-Digital";
  font-size: 5vh;
}
.display__speed {
  text-align: center;
  font-family: arial;
  font-style: italic;
  font-size: 1.25vh;
  line-height: 2vh;
  margin-top: 1vh;
}
.display__speed-value {
  margin: 0;
  font-style: normal;
  line-height: 5vh;
  font-family: "DS-Digital";
  font-size: 5vh;
}
.display__pause {
  position: relative;
  width: 5vh;
  text-align: center;
  font-family: arial;
  font-style: italic;
  font-size: 1.25vh;
  line-height: 2vh;
  margin: 5vh auto 0;
  color: #7E916C;
  fill: #7E916C;
}
.display__pause-svg {
  height: 5vh;
  width: 5vh;
  margin-top: 1vh;
}
.controls {
  position: relative;
  top: -30vh;
  height: 0;
  width: 36vh;
  margin: 0 auto;
}
.controls__start {
  display: inline-block;
  float: left;
  width: 9vh;
  height: 8vh;
  text-align: center;
  color: #041b17;
  font: bold small-caps 2.5vh Tahoma, Geneva, sans-serif;
}
.controls__start-button {
  height: 2.5vh;
  width: 8vh;
  border-radius: 2.5vh;
}
.controls__pause {
  display: inline-block;
  float: right;
  width: 9vh;
  height: 8vh;
  text-align: center;
  color: #041b17;
  font: bold small-caps 2.5vh Tahoma, Geneva, sans-serif;
}
.controls__pause-button {
  height: 2.5vh;
  width: 8vh;
  border-radius: 2.5vh;
}
.d-pad {
  position: relative;
  margin: 0 auto;
  width: 30vh;
  height: 30vh;
}
.d-pad__left {
  margin: 0;
  display: inline-block;
  height: 100%;
  width: 33%;
}
.d-pad__button-left {
  position: relative;
  margin: 100% 0;
  height: 33%;
  width: 99%;
  border-radius: 50%;
}
.d-pad__center {
  margin: 0;
  display: inline-block;
  height: 100%;
  width: 33%;
}
.d-pad__button-up {
  position: relative;
  margin: 0;
  height: 33%;
  width: 99%;
  border-radius: 50%;
}
.d-pad__arrows {
  position: relative;
  margin: 0;
  height: 34%;
  width: 100%;
  background: url(/rect-snek/static/media/arrows.88ec1662.svg) center no-repeat;
  background-size: 90%;
}
.d-pad__button-down {
  position: relative;
  margin: 0;
  height: 33%;
  width: 99%;
  border-radius: 50%;
}
.d-pad__right {
  margin: 0;
  display: inline-block;
  height: 100%;
  width: 33%;
}
.d-pad__button-right {
  position: relative;
  margin: 100% 0;
  height: 33%;
  width: 99%;
  border-radius: 50%;
}
